<template>
  <div class="bread">
    <Breadcrumb separator="<b style='color:black'>></b>">
      <BreadcrumbItem href="/home">
               当前位置
      </BreadcrumbItem>
      <a @click="refresh" href="javascript:void(0)">
        {{msg}}
      </a>
    </Breadcrumb>
    <p>{{msg}}</p>
  </div>
</template>
<script>
export default{
 data(){
   return {
     msg:""
   }
 },
  methods:{
    refresh(){
      location.reload();
    }
  },
  mounted(){
      let name=this.$route.name;
      switch (name){
        case 'userList':
          this.msg='用户列表'
          break;
        case 'userPower':
          this.msg='代理商权限组'
          break;
        case 'appList':
          this.msg='小程序列表'
          break;
        case 'terminal':
          this.msg='用户列表'
          break;
        case 'set':
          this.msg='设置'
          break;
        case 'web':
          this.msg='网站信息配置'
          break;
        case 'pay':
          this.msg='网站支付配置'
          break;
        case 'message':
          this.msg='信息通知配置'
          break;
          case 'userInfo':
          this.msg='代理商用户详情'
          break;
          case 'upgrade':
          this.msg='升级套餐'
          break;
      }
  }
}
</script>
<style scoped>
  p{
    margin: 25px 0;
    font-size: 26px;
    font-weight: 500;
  }
</style>



